import Vue from "vue";
import Router from "vue-router";
import home from "@/views/Home";
import create from "@/views/Create.vue";
import update from "@/views/Update.vue";
Vue.use(Router);
export default new Router({
  routes: [
    {
      path: "/",
      name: "home",
      component: home,
    },
    {
      path: "/create",
      name: "create",
      component: create,
    },
    {
      path: "/update/:id",
      name: "update",
      component: update,
    },
  ],
});
