<template>
  <div class="container" style="margin-top: 25px">
    <form @submit.prevent="submitBtnClick">
      <div class="field">
        <label class="label">Title</label>
        <div class="control">
          <input
            v-model="title"
            class="input"
            type="text"
            placeholder="Title" />
        </div>
      </div>

      <div class="field">
        <label class="label">Content</label>
        <div class="control">
          <textarea
            v-model="content"
            class="textarea"
            placeholder="Content"></textarea>
        </div>
      </div>
      <div class="field">
        <label class="label">Creater</label>
        <div class="control">
          <input
            class="input"
            v-model="creator"
            type="text"
            placeholder="Creater" />
        </div>
        <div class="btncontainer">
          <button
            type="submit"
            style="background-color: #e0954f"
            class="button is-link">
            Submit
          </button>
        </div>
      </div>
    </form>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      title: "",
      content: "",
      creator: "",
    };
  },
  methods: {
    submitBtnClick: async function () {
      try {
        const API_URL = "http://localhost:5000/posts";
        const body = {
          title: this.title,
          content: this.content,
          creator: this.creator,
        };
        const response = await axios.post(API_URL, body);
        console.log(response);
      } catch (error) {
        console.log(error);
      }
    },
  },
  computed: {
    postJson: function () {
      return JSON.parse(this.post);
    },
  },
};
</script>
<style>
.btncontainer {
  margin-top: 25px;
}
</style>
